.photoBlock {
  display: grid;
  gap: 16px;

  @include on-tablet {
    display: flex;
  }

  &__small-photos {
    display: flex;
    justify-content: center;
    gap: 8px;
    grid-row: 2;

    @include on-tablet {
      flex-direction: column;
      justify-content: flex-start;
      grid-row: 1;
    }

    &--photo {
      object-fit: contain;
      box-sizing: border-box;
      border: 1px solid $shadow-color;
      border-radius: 4px;
      width: 40px;
      height: 40px;
      padding: 5px;

      @include on-tablet {
        width: 35px;
        height: 35px;
      }

      @include on-desktop {
        width: 80px;
        height: 80px;
      }
    }

    &--photo-active {
      border-color: $text-color;
    }
  }

  &__big-photo {
    display: flex;
    justify-content: center;
    width: 100%;

    &--photo {
      object-fit: contain;
      height: 288px;

      @include on-desktop {
        height: 464px;
      }
    }
  }
}
