.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid $select-border-color;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  cursor: pointer;
  background-color: $bg-button-color;

  &__selected {
    border-color: $shadow-color;
  }

  &__disabled {
    cursor: not-allowed;
    pointer-events: none;
    opacity: 30%;
  }

  &:hover {
    border-color: $text-color;
  }
}

.cart-button {
  width: 100%;
  height: 48px;
  border-radius: 8px;
  background-color: $button-color;
  border: none;
  font-family: Mont-Regular, sans-serif;
  font-size: 14px;
  color: $bg-button-color;
  cursor: pointer;

  &__selected {
    border: 1px solid $shadow-color;
    background-color: $bg-button-color;
    color: $button-color;
  }

  &:hover {
    box-shadow: 0 3px 13px 0 $shadow-cart-color;
    border: 1px solid $button-color;
  }
}
